@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap");

body {
  font-size: 16px;
  margin: 0;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

$primary-color: #43c0f6;
$accent-color: #f91c85;
$accent-secondary-color: #a29f9f;
$placeholder-color: #a5a5a5;
$border-radius: 0.4rem;
$border-width: 0.15rem;

//  Style on Mobile Devices
@media screen and (max-width: 960px) {
  .header-bottom {
    background-position: 55% 0% !important;
  }
  section {
    padding: 40px 300px;
  }
  .section-1 {
    .icon-wrapper {
      width: 100%;
    }
  }
  .navigation {
    padding: 0 300px;
  }
}

@media screen and (min-width: 960px) and (max-width: 2440px) {
  html {
    font-size: 10px;
  }
  .header-bottom {
    background-image: none !important;
  }
  section {
    padding: 0px 1rem !important;
  }
  .section-1 {
    .icon-wrapper {
      width: 100%;
    }
  }
  .navigation {
    padding: 0 50px;
    .logo > a > img {
      height: 40px;
    }
    padding: 0px 1rem !important;
  }
}

::placeholder {
  padding-left: 0.5rem;
  text-align: left;
  color: $placeholder-color;
  text-transform: capitalize;
}

input {
  border: solid $border-width $accent-secondary-color;
  border-radius: $border-radius;
}

.button-one {
  background-color: $accent-color;
  color: #ffffff;
  width: 15rem;
  height: 2rem;
  border-radius: $border-radius;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
  text-transform: uppercase;
}

.button-two {
  background-color: #ffffff;
  color: #000000;
  width: 15rem;
  height: 2rem;
  border-radius: $border-radius;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.15);
  border: solid $border-width $primary-color;
  text-transform: uppercase;
}
