.slideshow {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  // align-items: center;
  .slide {
    // width: 100%;
    // height: 100%;
    img {
      width: 100%;
    }
  }
  .slide-controller {
    align-items: center;
    span.slide-dot {
      cursor: pointer;
      height: 15px;
      width: 15px;
      display: inline-block;
      border-radius: 50%;
      border: solid 0.2rem #717171;
      margin: 0 4px;
    }
  }
}
