.submission-item {
  display: flex;
  flex-direction: row;
  margin: 20px 0;
  align-items: center;

  .project-wrapper {
    display: grid;
    grid-template-columns: 1fr 5fr 1fr;
    grid-template-rows: 1fr;
    align-items: center;
    margin: 0 20px;
    padding: 10px 40px;
    width: 90%;
    border-radius: 20px;
    background-color: white;
    img {
      width: 80px;
      height: 80px;
    }
    .submission-message {
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: center;
    }
    .submission-timestamp {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }
  }
  .teacher-project-image {
    // display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 50px;
    display: none;
    button {
      cursor: pointer;
    }
  }
}

.active {
  display: flex !important;
}
