footer {
  width: 100vw;
  background-color: #b2e4fa;
  display: flex;
  justify-content: center;
  font-size: 16px;
  padding: 10rem;
  .footer-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
  }

  div {
    display: flex;
    flex-direction: column;
  }
}

a,
a:hover,
a:visited {
  text-decoration: none;
}
